html {
  font-family: Roboto, sans-serif;
}

:root {
  --link: #00acdc;
}

.page {
  background-color: #969696;
  margin: 0;
}

.header {
  background-color: #fff;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  padding-top: 10px;
  padding-left: 50px;
}

.header__nav {
  flex-direction: row;
  padding-right: 50px;
  display: flex;
}

.header__nav-link {
  color: #000;
  text-transform: uppercase;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-right: 20px;
  padding: 22.5px 0;
  font-size: 12px;
  font-weight: 800;
  position: relative;
  overflow: hidden;
}

.is-active, .is-active, .header__nav-link:hover {
  color: var(--link);
}

.is-active:after {
  content: "";
  background-color: var(--link);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.header .header__nav-link:nth-last-child {
  margin-right: 0;
}
